<template>
  <div class="container index-page">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="生活馆" name="lifeStore"></el-tab-pane>
      <!-- 精选 -->
      <el-tab-pane label="精选" name="pick">
        <!-- 精选头部轮播图 -->
        <el-carousel :interval="4000" type="card" height="200px">
          <el-carousel-item v-for="item in highPickAdv.banner_images" :key="item">
            <img :src="item.image">
          </el-carousel-item>
        </el-carousel>
      </el-tab-pane>
      <el-tab-pane label="探索" name="explore">生</el-tab-pane>
    </el-tabs>heheh
    <!-- 加载页面 -->
    <loading-page :isShow="true" :time="1000"/>
    <!-- tabbar -->
    <tabbar/>
  </div>
</template>
<script>
import api from "@/constant/api";
export default {
  data() {
    return {
      activeName: "lifeStore", //

      highPickAdv: {} // 精选头部广告
    };
  },
  created() {
    this.getProduct();
  },
  methods: {
    // 切换tab
    handleClick(tab, event) {},
    getProduct() {
      console.log(123);
      this.$axios.get(api.banners_handpick, {}).then(res => {
        console.log(res);
        if (res.status.code == 200) {
          this.highPickAdv = res.data;
        } else {
          alert(res.status.message);
        }
      });
    }
    //
  }
};
</script>
<style lang="scss" scoped>
@import "@/assets/css/base.scss";
.index-page {
}
</style>


